import { SET_APARTMENTS, SET_APARTMENT, FILTER_APARTMENTS, POST_APARTMENT, UPDATE_APARTMENT, DELETE_APARTMENT, LOADING_DATA, STOP_LOADING_UI, LOADING_UI, SET_ERRORS, CLEAR_ERRORS } from '../types';
import axios from 'axios';

// // Get all screams
export const getApartments = () => (dispatch) => {
    dispatch({ type: LOADING_DATA });
    axios.get('/apartments')
        .then(res => {
            dispatch({
                type: SET_APARTMENTS,
                payload: res.data
            })
            dispatch({ type: STOP_LOADING_UI });
        })
        .catch(err => {
            dispatch({
                type: SET_APARTMENTS,
                payload: []
            })
        })
}

// Post an apartment
export const postApartment = (newData) => (dispatch) => {
    dispatch({ type: LOADING_DATA });
    axios.post('/apartment', newData)
        .then(res => {
            console.log(res.data);
            dispatch({
                type: POST_APARTMENT,
                payload: res.data
            })
            dispatch(clearErrors())
        })
        .catch(err => {
            dispatch({
                type: SET_ERRORS,
                payload: err.response.data
            })
        })
}

// Delete an apartment
export const deleteApartment = (id) => (dispatch) => {
    dispatch({ type: LOADING_DATA });
    axios.delete(`/apartment/${id}`)
        .then(() => {
            dispatch({ type: DELETE_APARTMENT, payload: id })
        })
        .catch(err => console.log(err));
}

//Update an apartment
export const updateApartment = (id, newData) => (dispatch) => {
    dispatch({ type: LOADING_DATA });
    axios.patch(`/apartment/${id}`, newData)
        .then(res => {
            const result = {
                apartment : {
                    ...res.data.apartment,
                    id: id,
                },
                message: "Update successfully!"
            }
            dispatch({
                type: UPDATE_APARTMENT,
                payload: result
            })
            dispatch(clearErrors())
        })
        .catch(err => {
            dispatch({
                type: SET_ERRORS,
                payload: "Update failed!"
            })
        })  
}

//Filter
export const filter = (constraint) => (dispatch) => {
    dispatch({ type: LOADING_DATA });
    axios.get(`/apartmentsFilter/${constraint}`)
        .then(res => {
            dispatch({
                type: FILTER_APARTMENTS,
                payload: res.data
            })
            dispatch({ type: STOP_LOADING_UI });
        })
        .catch(err => {
            dispatch({
                type: FILTER_APARTMENTS,
                payload: []
            })
        })
}

// Load image
export const uploadImage = (formData, id) => (dispatch) => {
    dispatch({ type: LOADING_UI });
    axios.post(`/apartment/image/${id}`, formData)
        .then(res => {
            dispatch(getOne(id));
            dispatch({ type: STOP_LOADING_UI });
        })
        .catch(err => console.log(err));
}

export const getOne = (id) => (dispatch) => {
    dispatch({ type: LOADING_DATA });
    axios.get(`/apartment/${id}`)
        .then(res => {
            dispatch({
                type: SET_APARTMENT,
                payload: res.data
            });
        })
        .catch(err => console.log(err));
}

export const clearErrors = () => (dispatch) => {
    dispatch({ type: CLEAR_ERRORS });
}

