import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
// Date handler
import moment from 'moment';
// MUI
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Avatar from '@material-ui/core/Avatar';
import Tooltip from '@material-ui/core/Tooltip';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
// Components
import ModalUpdate from '../Modals/ModalUpdate';
// icons
import DeleteIcon from '@material-ui/icons/Delete';

// CSS-in-react
const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: "500px",
    marginLeft: "auto",
    marginRight: "auto"
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  cardActions: {
    justifyContent: "flex-end"
  },
  cardContent: {
    height: "350px"
  }
}));

const CardApartment = ({apartment, flag, deleteApartment}) => {

  const classes = useStyles();

  // Delete apartment
  const handleDelete = () => {
    deleteApartment(apartment.id);
  }

  // Check apartment card shows for which page
  if(apartment) {
    return (
      <Card className={classes.root} 
        raised>
        {/* Header */}
        <CardHeader
          avatar={
            <Avatar src="https://firebasestorage.googleapis.com/v0/b/mobile-2ef47.appspot.com/o/avatar.png?alt=media"/>
          }
          title={apartment.type}
          subheader={moment(apartment.dateCreate).format('DD/MM/YYYY')}
        />
        {/* Picture */}
        <CardMedia
          className={classes.media}
          image={apartment.url}
        />
        {/* Content */}
        <CardContent className={classes.cardContent}>

          <Typography variant="h6">
            Details
          </Typography>
            
          <Typography variant="subtitle1">
          &nbsp;• Type: {apartment.type}
          </Typography>

          <Typography variant="subtitle1">
          &nbsp;• Bedrooms: {apartment.bedrooms}
          </Typography>

          <Typography variant="subtitle1">
          &nbsp;• Price: {apartment.price}
          </Typography>

          <Typography variant="subtitle1">
          &nbsp;• Furniture type: {apartment.furniture}
          </Typography>
    
          <Typography variant="subtitle1">
          &nbsp;• Reporter: {apartment.reporter}
          </Typography>

          <Typography variant="subtitle1">
          &nbsp;• Created date: {moment(apartment.dateCreate).format('DD/MM/YYYY')}
          </Typography>
          
          {apartment.notes !== "Empty" && (
            <Typography variant="subtitle2" 
              color="secondary">
                &nbsp;<i>*Note: {apartment.notes}</i>
            </Typography>
          )}

        </CardContent>

        {/* Actions */}
        {flag === "apartments" && (
          <CardActions className={classes.cardActions} 
                disableSpacing>

            <ModalUpdate apartment={apartment}/>

            <Tooltip title="Delete" placement="top">
                <IconButton aria-label="delete" 
                  color="secondary" 
                  onClick={handleDelete}>
                    <DeleteIcon />
                </IconButton>
            </Tooltip>

          </CardActions>
        )}
      </Card>
    );
  } else {
    return (
      <>
      </>
    )
  }
}

export default CardApartment;