import React from 'react';
// MUI
import { makeStyles } from '@material-ui/core/styles';
import {Grid, Typography} from '@material-ui/core';
// Components
import CardApartment from "../CardApartment/CardApartment";
import Spinner from '../Spinner/Spinner';

// CSS-in-react
const useStyles = makeStyles((theme) => ({
    root: {
        margin: "2%"
    },
    hover: {
        '&:hover': {
            opacity: 0.8,
          }
    },
    container: {
        maxHeight: "1000px",
        width: "90%",
        marginLeft: "5%",
        marginRight: "5%",
        overflow: "auto",
    }
  }));
  
const HomeApartment = (props) => {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            <Typography variant="h4" 
                align="center">
                    New Rent Apartments
            </Typography>
            <br/>
            <Grid container 
                className={classes.container} 
                spacing={3} 
                alignItems="center">
                    {props.loading && <Spinner/>}
                    {/* Show apartments */}
                    {props.apartments.map(apartment => (
                        <Grid item 
                            xs={12} md={6} lg={4}   
                            className={classes.hover} 
                            key={apartment.id}>
                                <CardApartment apartment={apartment} 
                                    flag="home"
                                />
                        </Grid>
                    ))}
            </Grid>
        </div>
    );
}

export default HomeApartment;
