import React, { Component } from 'react';
import PropTypes from 'prop-types';
// Components
import StyledBanner from "../components/Banner/StyledBanner";
import Banner from '../components/Banner/Banner';
import ListApartment from '../components/ListApartment/ListApartment';
import SearchResult from '../components/SearchResult/SearchResult';
import Divider from '../components/Divider/Divider';
// Redux stuff
import { getApartments, deleteApartment } from '../redux/actions/dataAction';
import { connect } from 'react-redux';

export class Apartments extends Component {
    // Get all apartments initially
    componentDidMount() {
        this.props.getApartments();
    }

    render() {
        return (
            <div>

                <StyledBanner img="https://firebasestorage.googleapis.com/v0/b/mobile-2ef47.appspot.com/o/apartmentBanner.jpg?alt=media">
                    <Banner/>
                </StyledBanner>

                <Divider/>

                {/* Check show or hide search result */}
                {this.props.app.state.search && (
                    <>
                        <SearchResult apartmentsFilter={this.props.data.apartmentsFilter}/>
                        <Divider/>
                    </>
                )}

                <ListApartment apartments={this.props.data.apartments} 
                    loading={this.props.data.loading} 
                    deleteApartment={this.props.deleteApartment.bind(this)}
                />

            </div>
        )
    }
}
// Check type of data
Apartments.propTypes = {
    getApartments: PropTypes.func.isRequired,
    deleteApartment: PropTypes.func.isRequired,
    user: PropTypes.object.isRequired,
    UI: PropTypes.object.isRequired,
    data: PropTypes.object.isRequired
}
// Get data from redux to props
const mapStateToProps = (state) => ({
    user: state.user,
    UI: state.UI,
    data: state.data
});
// Get action from redux to props
const mapActionsToProps = {
    getApartments,
    deleteApartment
}

export default connect(mapStateToProps, mapActionsToProps)(Apartments);
