import {SET_APARTMENT, SET_APARTMENTS, POST_APARTMENT, DELETE_APARTMENT, LOADING_DATA, UPDATE_APARTMENT, FILTER_APARTMENTS} from '../types';

const initialState = {
    apartments: [],
    apartmentsFilter: [],
    apartment: {},
    loading: false,
    message: "",
};

export default function(state = initialState, action){
    let index;
    switch(action.type){
        case LOADING_DATA:
            return {
                ...state,
                loading: true
            }
        case SET_APARTMENTS:
            return {
                ...state,
                apartments: action.payload,
                loading: false
            }
        case SET_APARTMENT: 
            return {
                ...state,
                loading: false,
                message: "Loaded image",
                apartment: action.payload
            }
        case FILTER_APARTMENTS:
            return {
                ...state,
                apartmentsFilter: action.payload,
                loading: false
            }
        case POST_APARTMENT:
            state.apartments.push(action.payload);
            return {
                ...state,
                loading: false,
                message: action.payload.message
            }
        case UPDATE_APARTMENT: 
            index = state.apartments.findIndex(apartment => apartment.id === action.payload.apartment.id);
            state.apartments.splice(index, 1);
            state.apartments.push(action.payload.apartment);
           
            // Sort by date
            state.apartments.sort((a, b) => (a.dateCreate < b.dateCreate) ? 1 : (a.dateCreate === b.dateCreate) ? ((a.type > b.type) ? 1 : -1) : -1 )
            return {
                ...state,
                loading: false,
                apartmentsFilter: [],
                message: action.payload.message
            }
        case DELETE_APARTMENT:
            index = state.apartments.findIndex(apartment => apartment.id === action.payload);
            state.apartments.splice(index, 1);
            return {
                ...state,
                loading: false
            }
        default: 
            return state
    }
}