import React, { Component } from 'react';
import PropTypes from 'prop-types';
// MUI
import { withStyles } from '@material-ui/core/styles';
import {TextField, Button, Typography} from '@material-ui/core';
//Components
import Spinner from '../Spinner/Spinner';
// Redux stuff
import { postApartment, uploadImage } from '../../redux/actions/dataAction';
import { connect } from 'react-redux';

// CSS-in-react
const styles = (theme) => ({
  form: {
    '& > *': {
      margin: theme.spacing(1),
      width: '100%',
    },
  },
  customError: {
    color: "red",
    textAlign: "center",
    marginTop: 10
  }
});

class FormAdd extends Component {
    state = {
        type: "",
        bedrooms: "",
        price: 0,
        furniture: "",
        reporter: "",
        notes: "Empty",
        url: "",
        message: "",
        errors: {}
    }

    // Get errors after adding
    componentWillReceiveProps() {
        this.setState({
            ...this.state,
            message: this.props.data.message
        })
    }

    // Get data from typing to state
    handleChange = (event) => {
        this.setState({
            ...this.state,
            [event.target.name] : event.target.value
        });
    }

    // Upload image
    handleUpload = (e) => {
        e.preventDefault();
        const image = e.target.files[0];
        const formData = new FormData();
        formData.append('image', image, image.name);
        this.props.uploadImage(formData, this.props.data.apartments[this.props.data.apartments.length-1].apartment.id);
    }

    // Handle submiting
    handleSubmit = (e) => {
        e.preventDefault();

        let errors = {};
        // Check constraints
        if(this.state.type === "") {
            errors.type = "Type is required!"
        } else 
        if(this.state.bedrooms === "") {
            errors.bedrooms = "Bedrooms is required!"
        }
        if(this.state.price === 0) {
            errors.price = "Price is required!"
        }
        if(this.state.reporter === "") {
            errors.reporter = "Reporter is required!"
        }

        if(errors !== {}){
            this.setState({
                ...this.state,
                errors
            })
        } else {
            const newData = {
                type: this.state.type,
                bedrooms: this.state.bedrooms,
                price: this.state.price,
                furniture: this.state.furniture,
                reporter: this.props.user.credentials.email,
                notes: this.state.notes
            };
            // Start adding
            this.props.postApartment(newData);
        }
    }


    render() {
        const {classes} = this.props;

        return (
            <form className={classes.form} 
                noValidate 
                autoComplete="off" 
                onSubmit={this.handleSubmit}>

                    {this.state.errors.type ? (
                        <TextField error 
                            name="type" 
                            helperText={this.state.errors.type} 
                            required label="1. Type" 
                            variant="filled" 
                            onChange={this.handleChange}    
                        />
                    ) : (
                        <TextField name="type" 
                            required label="1. Type" 
                            variant="filled" 
                            onChange={this.handleChange}
                        />
                    )}

                    {this.state.errors.bedrooms ? (
                        <TextField error 
                            name="bedrooms" 
                            helperText={this.state.errors.bedrooms} 
                            required 
                            label="2. Bedrooms" 
                            variant="filled"  
                            onChange={this.handleChange}
                        />
                    ) : (
                        <TextField name="bedrooms" 
                            required 
                            label="2. Bedrooms" 
                            variant="filled"  
                            onChange={this.handleChange}
                        />
                    )}

                    {this.state.errors.price ? (
                        <TextField error 
                            name="price" 
                            type="number" 
                            helperText={this.state.errors.price} 
                            required 
                            label="3. Price" 
                            variant="filled" 
                            onChange={this.handleChange}
                        />
                    ) : (
                        <TextField name="price" 
                            type="number" 
                            required 
                            label="3. Price" 
                            variant="filled"
                            onChange={this.handleChange}
                        />
                    )}

                    <TextField name="furniture" 
                        label="4. Furniture" 
                        variant="filled"  
                        onChange={this.handleChange}
                    />

                    {this.state.errors.reporter ? (
                        <TextField error 
                            name="reporter" 
                            helperText={this.state.errors.reporter} 
                            required 
                            label="5. Reporter" 
                            variant="filled" 
                            onChange={this.handleChange}
                        />
                    ) : (
                        <TextField name="reporter" 
                            required 
                            label="5. Reporter" 
                            variant="filled" 
                            onChange={this.handleChange}
                        />
                    )}

                    <Button type="submit" 
                        variant="contained" 
                        color="primary">
                            Create information
                    </Button>
                    {/* Message and spinner */}
                    {this.state.message && (
                        <>
                            <Typography>
                                Add apartment image:
                            </Typography>
                            <TextField type="file" 
                                name="image" 
                                onChange={this.handleUpload}
                            />
                        </>
                    )}
                    {this.state.message === "Loaded image" && (
                        <Typography variant="body2" 
                            className={classes.customError}>
                                {this.state.message}
                        </Typography>
                    )}
                    {this.props.data.loading && <Spinner/>}

            </form>
        );
    }
}
// Check type of data
FormAdd.propTypes = {
    uploadImage: PropTypes.func.isRequired,
    postApartment: PropTypes.func.isRequired,
    UI: PropTypes.object.isRequired,
    user: PropTypes.object.isRequired
}
// Get data from redux to props
const mapStateToProps = (state) => ({
    UI: state.UI,
    user: state.user,
    data: state.data
});
// Get action from redux to props
const mapActionsToProps = {
    postApartment,
    uploadImage
}

export default connect(mapStateToProps, mapActionsToProps)(withStyles(styles)(FormAdd));