import React, { Component } from 'react';
import PropTypes from 'prop-types';
// MUI
import { withStyles } from '@material-ui/core/styles';
import {TextField, Button, CardMedia, Typography } from '@material-ui/core';
// Components
import Spinner from '../Spinner/Spinner';
// Redux stuff
import { getOne, uploadImage, updateApartment, getApartments } from '../../redux/actions/dataAction';
import { connect } from 'react-redux';

// CSS-in-react
const styles = (theme) => ({
  form: {
    '& > *': {
      margin: theme.spacing(0.5),
      width: '100%',
    },
  },
  media: {
    height: 0,
    paddingTop: '45%',
  }
});

class FormUpdate extends Component {
    
    state = {
        type: "",
        bedrooms: "",
        price: 0,
        furniture: "",
        reporter: "",
        notes: "Empty",
        message: "",
        url: null,
        uploadedImage: null,
        errors: {},
    }

    // Set data initially
    componentDidMount() {
        const {apartment} = this.props;
        
        this.setState({
            ...this.state,
            type: apartment.type,
            bedrooms: apartment.bedrooms,
            price: apartment.price,
            furniture: apartment.furniture,
            reporter: apartment.reporter,
            notes: apartment.notes,
            url: apartment.url
        });
    }

    // Get data from typing
    handleChange = (event) => {
        this.setState({
            ...this.state,
            [event.target.name] : event.target.value
        });
    }

    // Handle uploading
    handleUpload = (e) => {
        e.preventDefault();
        const image = e.target.files[0];
        this.setState({
            ...this.state,
            uploadedImage: image
        })
    }

    // Handle submiting
    handleSubmit = (e) => {
        e.preventDefault();
        let errors = {};
        // Check constraints
        if(this.state.type === "") {
            errors.type = "Type is required!"
        } else 
        if(this.state.bedrooms === "") {
            errors.bedrooms = "Bedrooms is required!"
        }
        if(this.state.price === 0) {
            errors.price = "Price is required!"
        }
        if(this.state.reporter === "") {
            errors.reporter = "Reporter is required!"
        }

        if(errors.type || errors.bedrooms || errors.price || errors.reporter){
            this.setState({
                ...this.state,
                errors
            })
        } else {
            const updateData = {
                type: this.state.type,
                bedrooms: this.state.bedrooms,
                price: this.state.price,
                furniture: this.state.furniture,
                reporter: this.state.reporter,
                notes: this.state.notes,
                url: this.state.url
            };
            console.log(this.props.UI);
            // Check if user change image
            if(this.state.uploadedImage !== null) {
                const formData = new FormData();
                formData.append('image', this.state.uploadedImage, this.state.uploadedImage.name);
                this.props.uploadImage(formData, this.props.apartment.id);
            }
           
            // Start updating
            this.props.updateApartment(this.props.apartment.id, updateData);
        }
    }

    render() {
        console.log(this.props.UI);
        const {classes} = this.props;

        // Check if data load completely
        if(this.props.data.loading) {
            return (
                <Spinner/>
            )
        } else {
            return (
                <>
                {this.props.data.message === "Update successfully!" && <Typography variant="h5" align="center" color="secondary">{this.props.data.message}</Typography>}
                <form className={classes.form} 
                    noValidate 
                    autoComplete="off" 
                    onSubmit={this.handleSubmit}>
                        {this.state.errors.type ? (
                            <TextField name="type" 
                                error 
                                helperText={this.state.errors.type} 
                                required 
                                label="1. Type" 
                                variant="filled" 
                                onChange={this.handleChange} 
                                value={this.state.type}
                            />
                        ) : (
                            <TextField name="type" 
                                required 
                                label="1. Type" 
                                variant="filled" 
                                onChange={this.handleChange} 
                                value={this.state.type}
                            />
                        )}

                        {this.state.errors.bedrooms ? (
                            <TextField name="bedrooms" 
                                error 
                                helperText={this.state.errors.bedrooms} 
                                required 
                                label="2. Bedrooms" 
                                variant="filled"  
                                onChange={this.handleChange} 
                                value={this.state.bedrooms}
                            />
                        ) : (
                            <TextField name="bedrooms" 
                                required 
                                label="2. Bedrooms" 
                                variant="filled"  
                                onChange={this.handleChange} 
                                value={this.state.bedrooms}
                            />
                        )}

                        {this.state.errors.price ? (
                            <TextField name="price" 
                                error 
                                helperText={this.state.errors.price}  
                                type="number" 
                                required 
                                label="3. Price" 
                                variant="filled"  
                                onChange={this.handleChange} 
                                value={this.state.price}
                            />
                        ) : (
                            <TextField name="price" 
                                type="number" 
                                required 
                                label="3. Price" 
                                variant="filled"  
                                onChange={this.handleChange} 
                                value={this.state.price}
                            />
                        )}

                        <TextField name="furniture" 
                            label="4. Furniture" 
                            variant="filled"  
                            onChange={this.handleChange} 
                            value={this.state.furniture}
                        />

                        {this.state.errors.reporter ? (
                            <TextField name="reporter" 
                                error 
                                helperText={this.state.errors.reporter} 
                                required 
                                label="5. Reporter" 
                                variant="filled"  
                                onChange={this.handleChange} 
                                value={this.state.reporter}
                            />
                        ) : (
                            <TextField name="reporter" 
                                required 
                                label="5. Reporter" 
                                variant="filled"  
                                onChange={this.handleChange} 
                                value={this.state.reporter}
                            />
                        )}

                        <TextField name="notes" 
                            label="6. Notes" 
                            variant="filled"  
                            onChange={this.handleChange} 
                            value={this.state.notes && this.state.notes}                           
                        />


                        {this.state.url !== null && (
                            <CardMedia
                                className={classes.media}
                                image={this.state.url}
                                onClick={this.handleMediaClick}
                            />
                        )}

                        <TextField type="file" 
                            name="image" 
                            onChange={this.handleUpload}
                        />

                        <Button type="submit" 
                            variant="contained" 
                            color="primary">
                                Update
                        </Button>
                        {/* Message and spinner */}
                        {this.props.data.loading && <Spinner/>}
                        {this.props.UI.errors && 
                            <h2>
                                Update failed!
                            </h2>}

                </form>
                </>
            );
        }
    }
}
// Check type of data
FormUpdate.propTypes = {
    uploadImage: PropTypes.func.isRequired,
    getOne: PropTypes.func.isRequired,
    updateApartment: PropTypes.func.isRequired,
    getApartments: PropTypes.func.isRequired,
    UI: PropTypes.object.isRequired,
    user: PropTypes.object.isRequired
}
// Get data from redux to props
const mapStateToProps = (state) => ({
    UI: state.UI,
    user: state.user,
    data: state.data
});
// Get action from redux to props
const mapActionsToProps = {
    getOne,
    updateApartment,
    uploadImage,
    getApartments
}

export default connect(mapStateToProps, mapActionsToProps)(withStyles(styles)(FormUpdate));